@font-face {
  font-family: "Lato";
  src: local("Lato"), url("../fonts/Lato-Regular.woff2") format("woff2"), url("../fonts/Lato-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Lato-Bold";
  src: local("Lato-Bold"), url("../fonts/Lato-Bold.woff2") format("woff2"), url("../fonts/Lato-Bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Rubik";
  src: local("Lato"), url("../fonts/Rubik-Regular.woff2") format("woff2"), url("../fonts/Rubik-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Rubik-Bolt";
  src: local("Lato"), url("../fonts/Rubik-Bold.woff2") format("woff2"), url("../fonts/Rubik-Bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: "Lato", arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #1A212F; }

main {
  flex-grow: 1; }

.content {
  flex: 1 0 auto; }

.overlay {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

img {
  max-width: 100%;
  height: auto; }

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer; }

span {
  display: block; }

button:active,
button:focus {
  outline: none; }

a:focus,
button:focus {
  outline: #102654 solid 1px; }

button::-moz-focus-inner {
  border: 0; }

.header__nav {
  max-width: 1366px;
  min-height: 76px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding: 0 98px; }

.header__navigation {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  list-style: none; }

.header__navigation li {
  margin-right: 30px; }
  .header__navigation li:hover {
    opacity: 0.5; }

.header__navigation a {
  font-family: "Lato", arial, sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px; }

.preview__info {
  max-width: 1366px;
  margin: 0 auto;
  background-image: url("../img/vector-backround.png"), url("../img/cat-backround.png");
  background-repeat: no-repeat;
  min-height: 504px;
  padding-top: 153px;
  padding-bottom: 153px;
  padding-left: 198px; }

.preview__info span {
  font-family: "Lato", arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px; }

.preview__info h2 {
  font-family: "Rubik-Bolt", arial, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 62px;
  line-height: 73px; }

.preview__info p {
  font-family: "Lato", arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  margin-bottom: 40px; }

.preview__button {
  position: relative;
  width: 206px;
  height: 50px;
  background: #fffefd;
  box-shadow: 0px 5px 25px rgba(26, 33, 47, 0.1);
  border-radius: 25px;
  font-family: "Lato", arial, sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  text-align: start;
  padding: 16px 30px; }
  .preview__button::after {
    position: absolute;
    content: "";
    width: 36px;
    height: 36px;
    background-image: url("../img/paw.png");
    top: 8px;
    right: 10px; }
  .preview__button:hover {
    opacity: 0.5; }
